<template>
    <div>
        <div class="tile is-ancestor">
            <div class="tile is-parent">
                <article class="tile is-child box setting-box" v-loading="loading">
                    <div class="setting-panel" v-for="(item, index) in list">
                        <div class="setting-header">
                            <div class="filter-item setting-title">
                                <label class="control-label">
                                    {{ item.selcetData.name + '&nbsp;&nbsp;&nbsp;&nbsp;[' + item.selcetData.title + ']：'  }}
                                </label>
                            </div>
                            <div class="filter-item setting-button">
                                <!--<el-radio-group v-model="item.status.action" @change="togglePlatformAction(item)">-->
                                <el-radio-group v-model="item.status.action">
                                    <el-radio-button label="1">Android</el-radio-button>
                                    <!--<el-radio-button label="2">IOS</el-radio-button>-->
                                </el-radio-group>
                                <el-button v-show="!item.status.editing"
                                           @click="editAction(item, index)"
                                           type="primary">编辑</el-button>
                                <el-button v-show="item.status.editing"
                                           @click="saveAction(item, index)"
                                           type="primary">保存</el-button>
                                <el-button v-show="item.status.editing"
                                           @click="cancelAction(item, index)"
                                           type="warning">取消</el-button>
                                <el-button @click="editHistory(item, index)">修改记录</el-button>
                                <el-button type="info"
                                           icon="setting"
                                           :disabled="item.status.value==''"
                                           @click="setting(item, index)"></el-button>
                            </div>
                        </div>
                        <div class="setting-container">
                            <edit-code v-model="item.status.value"
                                       :editable="item.status.editing"></edit-code>
                            <div class="setting-history-box" v-show="item.status.showHistory">
                                <div class="history-header">
                                    <div class="header-title"></div>
                                    <div class="header-close"></div>
                                </div>
                                <div class="history-body">
                                    <div class="history-old">
                                    </div>
                                    <div class="history-new"></div>
                                    <div class="history-log"></div>
                                </div>
                            </div>
                            <edit-history v-model="item.status.showHistory"
                                          :list="item.history"
                                          :data="item.data"></edit-history>
                        </div>
                    </div>
                </article>
            </div>
        </div>
        <global-dialog ref="globalDialog" @refersh="getList"></global-dialog>
    </div>
</template>

<style scrop lang="less" type="text/scss">
    .filter-item {
        display: inline-block;
        margin-right: 20px;
    }

    .sensitiveTip {
        line-height: 28px;
    }

    .setting-box {
        min-height: 500px;
    }

    .setting-panel {
        position: relative;
        margin-bottom: 40px;
        //border-bottom: 1px solid rgb(209, 229, 229);

        .setting-header {
            height: 36px;
            line-height: 36px;
            margin-bottom: 10px;
            .setting-button {
                float: right;
            }
        }

        .setting-container {
            width: 100%;
            height: 240px;
            position: relative;

            .setting-history-box {
                position: absolute;
                width: 100%;
                height: 240px;
                top: 0px;
                border: 1px solid rgb(209, 229, 229);
                border-radius: 4px;
                background-color: #fff;
                box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, .12), 0px 0px 6px 0px rgba(0, 0, 0, .04);

                &:before {
                    content: '';
                    display: block;
                    width: 20px;
                    height: 20px;
                    background-color: white;
                    position: absolute;
                    right: 110px;
                    top: -10px;
                    transform: rotate(45deg);
                    border-top: 1px solid rgb(209, 229, 229);
                    border-left: 1px solid rgb(209, 229, 229);
                }
            }



            .J-container {
                display: block;
                padding: 10px;
                margin: 0 0 10.5px;
                font-size: 13px;
                line-height: 1.52857143;
                word-break: break-all;
                word-wrap: break-word;
                color: #3a3f51;
                background-color: #f5f5f5;
                border: 1px solid #cccccc;
                border-radius: 4px;
                height: 100%;
                overflow: auto;

                .J-static-code {
                    padding: 0;
                    font-size: inherit;
                    color: inherit;
                    white-space: pre-wrap;
                    background-color: transparent;
                    border-radius: 0;
                    min-height: 100%;
                }
            }
        }
    }


</style>

<script>
	import moment from '../../module/MyMoment';
	import EditCode from './EditCode';
	import EditHistory from './EditHistory';
	import GlobalDialog from './GlobalDialog';
    import QuRequest from '../../core/QuRequest';
    const quRequest = new QuRequest();

	export default {
		components: {
            EditCode,
            EditHistory,
            GlobalDialog
        },
		created() {
			this.filter = this.$queryFilter();
			this.$updateRouter(this.filter);
		},
		mounted() {
			this.getList();
		},
		data() {
			return {
				form: {
					'name': '',
					'title': '',
					'value': '',
					'type': ''
				},
				rules: {
                    name: [
						{ required: true, message: '请输入名称', trigger: 'blur' }
					]
				},
                list: [],
				dialogFormVisible: false,
				loading: false
			}
		},
		methods: {
			editAction(item, index){
				item.status.editing = true;
				// let node = document.querySelector('.edit-container-' + index);
            },
			saveAction(item){
                var formData = Object.assign({}, item.selcetData);
                var inputValue = item.status.value;
                formData.value = inputValue;
                let isOk = true;
                if(item.status.type === 'json'){
                    try {
                        console.log(inputValue)
                        JSON.parse(inputValue.replace(/(^\s+)|(\s+$)/g, ""));
                    } catch (error) {
                        isOk = false;
                        this.$message({
                            type: 'error',
                            message: 'JOSN语法错误，请检查'
                        });
                    }
                }
                if(isOk){
                    quRequest.send(quRequest.Apis.qutuiba.config.modify, formData).then( res => {
                        this.$message({
                            type: 'success',
                            message: res.message
                        });
                        if(!res.code) {
                            item.selcetData.update_time = res.data.modify_time;
                            console.log(item.data.update_time)
                            item.status.editing = false;
                            item.selcetData.value = inputValue;
                        }

                    }).catch((res) => {
                        this.$message({
                            message: res.message,
                            type: 'error'
                        })
                    })
                }
            },
			cancelAction(item){
				item.status.editing = false;
				item.status.value = item.status.backValue;
            },
			editHistory(item){
			    let name = item.data[item.status.action].name;
			    let platform = item.data[item.status.action].platform;
                quRequest.send(quRequest.Apis.qutuiba.config.getConfigLog, {
                    name: name,
                    platform: platform
                }).then(res => {
                    item.status.showHistory = !item.status.showHistory;
                    item.history = res.data;
                })
            },
			setting(item){
                this.$refs.globalDialog.showDialog(item.selcetData);
            },
			getList(){
				this.loading = true;
                quRequest.send(quRequest.Apis.qutuiba.config.list)
                    .then( res => {
					if(res && res.code == 0){
						let data = res.data;
						let result = [];
						let defaultAction = 1;
						for (let i = 0; i < data.length; i++) {
							let item = data[i];
							let defaultItem = item[defaultAction];
							let codeStr = defaultItem.value.toString();
							result.push({
								data: item,
                                selcetData: defaultItem,
								status: {
									name: defaultItem.name,
									action: defaultAction,
									editing: false,
									// 取得第一个key 对应的数据
									type: defaultItem.type || 'text',
                                    showHistory: false,
									value: codeStr,
                                    backValue: codeStr
								},
                                history: []
							});
						}
						this.list = result;
						this.loading = false;
                    }
                }).catch((err) => {
					this.$message({
						message: err.message,
						type: 'error'
					});
				})
            },
            togglePlatformAction(info){
			    let action = info.status.action;
			    let item = info.selcetData;
                let codeStr = item.value.toString();
			    let status = {
                    name: item.name,
                    action: action,
                    editing: false,
                    // 取得第一个key 对应的数据
                    type: item.type || 'text',
                    showHistory: false,
                    value: codeStr,
                    backValue: codeStr
                }
                info.status = status;
            }
		}
	}

</script>


