<template>
    <el-dialog title="配置" v-model="dialogFormVisible">
        <el-form :model="form" ref="dialogForm"
                 label-position="top"
                 :rules="rules"
                 label-width="60px">
            <el-form-item label="名称:" prop="title">
                <el-input v-model="form.title" ></el-input>
            </el-form-item>
            <el-form-item label="类型:">
                <el-select v-model="form.type">
                    <el-option label="默认" value=""></el-option>
                    <el-option label="json" value="json"></el-option>
                    <el-option label="html" value="html"></el-option>
                </el-select>
            </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogSetting">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import QuRequest from '../../core/QuRequest';
    const quRequest = new QuRequest();

	export default {
		data() {
			return {
				form: {
					'name': '',
					'title': '',
					'value': '',
					'type': ''
				},
				rules: {
					title: [
						{ required: true, message: '请输入名称', trigger: 'blur' }
					]
				},

				dialogFormVisible: false,
				loading: false
			}
		},
		methods: {
            showDialog(data){
                console.log(data);
                if(data){
                    this.form = {
                        'name': data.name,
                        'value': data.value,
                        'type': data.type,
                        'title':data.title,
                    }
                }
                this.dialogFormVisible = true;
            },
            hideDialog(){
                this.dialogFormVisible = false;
            },
			dialogSetting(){
                this.$refs['dialogForm'].validate((valid) => {
                    if (valid) {
                        quRequest.send(quRequest.Apis.qutuiba.config.modify, this.form).then( res => {
                            this.$message({
                                type: 'success',
                                message: '修改成功'
                            });
                            this.$emit('refersh');
                            this.hideDialog();
                        })
                    }
                })

            }
		}
	}

</script>


