<template>
    <div class="setting-history-box" v-show="value">
        <div class="history-header">
            <div class="header-title">
                {{data.name}}&nbsp;|&nbsp;{{data.member_id}}
            </div>
            <div class="header-close" @click="hide">
                <i class="fa fa-times fa-lg" aria-hidden="true"></i>
            </div>
        </div>
        <div class="history-body">
            <div class="history-new">
                <div class="history-content" v-text="selectData.new_data"></div>
            </div>
            <div class="history-old">
                <div class="history-content" v-text="selectData.old_data"></div>
            </div>
            <div class="history-log">
                <div class="history-content">
                    <div class="log-item"
                         v-for="(item, index) in list"
                         :class="[ index === selectIndex ? 'selected': '']"
                         v-text="item.create_time" @click="selectItem(index)"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scrop lang="less">
    .setting-history-box {
        position: absolute;
        width: 100%;
        height: 240px;
        top: 0px;
        border: 1px solid rgb(209, 229, 229);
        border-radius: 4px;
        background-color: #fff;
        box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, .12), 0px 0px 6px 0px rgba(0, 0, 0, .04);

        &:before {
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            background-color: white;
            position: absolute;
            right: 110px;
            top: -10px;
            transform: rotate(45deg);
            border-top: 1px solid rgb(209, 229, 229);
            border-left: 1px solid rgb(209, 229, 229);
        }

        .history-header {
            border-bottom: 1px solid rgb(209, 229, 229);
            padding-top: 10px;
            padding-bottom: 5px;
            font-size: 1rem;
            position: relative;
            .header-title {
                padding-left: 10px;
                padding-right: 10px;
            }

            .header-close {
                position: absolute;
                right: 10px;
                top: 10px;

            }
        }

        .history-body {
            font-size: 0px;
            height: 200px;

            .history-new,
            .history-old,
            .history-log {
                display: inline-block;
                vertical-align: top;
                font-size: 1rem;
                height: 100%;
                padding: 5px 10px;
                position: relative;
            }

            .history-content {
                width: 100%;
                height: 100%;
                overflow: auto;
                white-space: pre-wrap;
            }

            .tip {
                position: absolute;
                bottom: 5px;
                right: 10px;
                padding: 4px 8px;
                font-size: 1rem;
                background: #000;
                background: rgba(0, 0, 0, 0.3);
                border-radius: 3px;
                color: #fff;
                box-sizing: initial;
            }

            .history-new {
                width: 40%;
                background: #FFFFCC;
                border-right: 1px solid rgb(209, 229, 229);
                height: 100%;

                &:after {
                    content: 'new';
                    .tip;
                }
            }

            .history-old {
                width: 40%;
                background: #F9F2F4;
                color: #C82A52;
                border-right: 1px solid rgb(209, 229, 229);

                &:after {
                    content: 'old';
                    .tip;
                }
            }

            .history-log {
                width: 20%;

                .log-item {
                    &:hover {
                        background-color: rgba(0, 0, 0, .15);
                    }

                    &.selected {
                        background-color: #01CA90;
                    }
                }

            }

        }
    }

</style>

<script>
    import QuRequest from '../../core/QuRequest';
    const quRequest = new QuRequest();

    export default {
        props: ['list', 'data', 'value'],
        data() {
            return {
                selectIndex: 0
            }
        },
        computed: {
            selectData(){
                if (this.list && this.list.length > 0) {
                    return this.list[this.selectIndex]
                }
                return {}
            }
        },
        methods: {
            selectItem(index){
                this.selectIndex = index;
            },
            hide(){
                this.$emit('input', false)
            }
        }
    }

</script>


