<template>
    <div class="setting-container">
        <pre class="J-container J-static-container"><code
                class="J-static-code"
                v-text="value"
                @blur="changed"
                @paste="changed"
                @delete="changed"
                :contenteditable="editable">
            </code>
        </pre>
    </div>
</template>

<style scrop lang="less" scoped>

    .setting-container {
        width: 100%;
        height: 240px;

        .J-container {
            display: block;
            padding: 10px;
            margin: 0 0 10.5px;
            font-size: 13px;
            line-height: 1.52857143;
            word-break: break-all;
            word-wrap: break-word;
            color: #3a3f51;
            background-color: #f5f5f5;
            border: 1px solid #cccccc;
            border-radius: 4px;
            height: 100%;
            overflow: auto;

            .J-static-code {
                padding: 0;
                font-size: inherit;
                color: inherit;
                white-space: pre-wrap;
                background-color: transparent;
                border-radius: 0;
                min-height: 100%;
            }
        }
    }
</style>

<script>
    export default {
        props: ['value', 'editable'],
        mounted() {
            //this.props.value = this.props.value.replace(/ /g,"")
            this.editorNode = this.$el.querySelector('.J-static-container');
            if(this.editorNode){
                this.editorNode.addEventListener('paste', this.pasteAction);
            }
        },
        beforeDestroy(){
            if(this.editorNode){
                this.editorNode.removeEventListener('paste', this.pasteAction)
            }
        },
        methods: {
            pasteAction(e){
                // cancel paste
                e.preventDefault();

                // get text representation of clipboard
                let text = e.clipboardData.getData("text/plain");

                // insert text manually
                document.execCommand("insertHTML", false, text);
            },
            changed(event){
                let newValue = event.target.innerText;
                console.log(newValue);
                this.$emit('input', newValue)
            }
        }
    }

</script>


